export default {
	data() {
		return {
			paramsArr: [
				{
					name: '所在区县',
					key: 'field02'
				},
				{
					name: '安全预警原因',
					key: 'field03'
				},
				{
					name: '安全预警级别',
					key: 'field04'
				},
				{
					name: '当前状态',
					key: 'field05'
				},
				{
					name: '安全情况',
					key: 'field06'
				}
			],
			dataArr: []
		};
	},
	mounted(){
		this.getData()
	},
	methods: {
		handleCollapse(item, index) {
			this.$set(item, 'isActive', !item.isActive);
		},
		getData(){
			this.$HTTP({
				url:'/safeData/list',
				params:{
					"field01":uni.getStorageSync('userId'),
					"pageNum":"",
					"pageSize":""
				},
				successCallback:({data})=>{
					if(data.code == 0){
						let arr = data.data.list.map(item=>{
							return{
								...item,
								title:new Date(item.addTime).Format('yyyy-MM-dd')
							}
						})
						this.dataArr = arr
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
				}
			})
		},
	}
};
